import React, { Fragment } from 'react'
import PropTypes from 'prop-types'
import { IntlProvider, addLocaleData } from 'react-intl'
import HeroImage from "../../images/wattys_splash_hero.png"

// Locale data
import enData from 'react-intl/locale-data/en'
import ptData from 'react-intl/locale-data/pt'
import deData from 'react-intl/locale-data/de'
import esData from 'react-intl/locale-data/es'
import filData from 'react-intl/locale-data/fil'
import frData from 'react-intl/locale-data/fr'
import idData from 'react-intl/locale-data/id'
import itData from 'react-intl/locale-data/it'
import mlData from 'react-intl/locale-data/ml'
import nlData from 'react-intl/locale-data/nl'
import trData from 'react-intl/locale-data/tr'

// Messages
import en from '../../i18n/en.json'
import pt from '../../i18n/pt.json'
import de from '../../i18n/de.json'
import es from '../../i18n/es.json'
import fil from '../../i18n/fil.json'
import fr from '../../i18n/fr.json'
import id from '../../i18n/id.json'
import it from '../../i18n/it.json'
import ml from '../../i18n/ml.json'
import nl from '../../i18n/nl.json'
import tr from '../../i18n/tr.json'

// Style
import "../../styles/global.css"
// Components
import Header from '../Header'
import Footer from '../Footer'

const messages = { en, pt, de, es, fil, fr, id, it, ml, nl, tr }

addLocaleData([...enData, ...ptData, ...deData, ...esData, ...filData, ...frData, ...idData, ...itData, ...mlData, ...nlData, ...trData])

const Layout = ({ locale, children }) => (
  <IntlProvider locale={locale} messages={messages[locale]}>
    <Fragment>
        <div class="container">
        <Header />

        <img src={HeroImage} alt="Hero Image" />

         <div class="container-inner">
          {children}
          </div>
        </div>
      <Footer />
    </Fragment>
  </IntlProvider>
)

Layout.propTypes = {
  locale: PropTypes.string.isRequired,
  children: PropTypes.oneOfType([
    PropTypes.node,
    PropTypes.arrayOf(PropTypes.node)
  ]).isRequired
}

export default Layout